iframe {
  border: none;
}

.clear {
  clear: both;
}

.abovefold {
  .transition-all;
  visibility: visible;
  opacity: 1;
  z-index: 1;
  &.affix {
    visibility: hidden;
    opacity: 0;
  }
  a {
    margin: 0;
    padding: 12px 0;
    font-size: 30px;
    text-align: center;
    color: white;
    cursor: pointer;
    &:hover {
      text-decoration: none;
    }
    .fa {
      position: absolute;
      margin-left: 10px;
      animation: moving-arrow .8s infinite;
    }
  }
}

section {
  position: relative;
  padding: 100px 0;
  &#featured {
    padding: 1px 0;
    .container {
      width: 100%;
    }
    .row > div {
      padding: 1px;
      & > span {
        .bg-featured;
        padding: 20px 0;
        font-size: 16px;
        font-weight: bold;
        color: @gray;
      }
      & > div {
        position: relative;
        .bg-featured;
        height: 300px;
        padding: 0 20px;
        overflow: hidden;
        & > div {
          height: 280px;
          ul {
            display: block;
            margin: 0;
            padding: 0;
            list-style: none;
            text-align: justify;
            * {
              display: block;
            }
            li {
              a {
                padding-bottom: 15px;
                &:hover {
                  opacity: .8;
                  color: @theme-primary;
                  text-decoration: none;
                  & > *:first-child {
                    text-decoration: underline;
                  }
                }
                span {
                  overflow: hidden;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                }
                img {
                  width: 100%;
                  margin: 5px 0;
                }
              }
              &:last-child {
                a {
                  padding-bottom: 0;
                }
              }
            }
          }
        }
        iframe {
          margin-bottom: 10px;
          padding: 0;
        }
        p {
          width: 100%;
          float: left;
          text-align: justify;
          &:last-of-type {
            margin-bottom: 0;
          }
        }
      }
    }
  }
  &#content {
    img {
      display: block;
      margin: auto;
      margin-bottom: 10px;
      &.contentimage {
        width: 100%;
      }
    }
    h2 {
      margin-top: 50px;
      color: @theme-secondary;
    }
    select {
      cursor: pointer;
    }
    .date-control {
      display: block;
      width: 100%;
      select.form-control {
        display: inline-block;
        width: auto;
      }
    }
    .radio label {
      &:not(:last-child) {
        margin-right: 20px;
      }
      input[type="radio"] {
        cursor: pointer;
      }
    }
    .identity {
      border-right: 1px solid @gray-lighter;
    }
    .identity, .billing {
      margin-top: 20px;
    }
    .variables {
      border-top: 1px solid @gray-lighter;
      border-bottom: 1px solid @gray-lighter;
      margin: 10px 0;
      padding: 10px 0;
    }
    canvas#simulation {
      margin-top: 40px;
    }
  }
  &#customer {
    .row > * {
      &:first-child {
        a {
          border-bottom: 1px solid @gray-lighter;
          padding-top: 10px;
          padding-bottom: 10px;
          color: @gray;
          text-decoration: none;
          cursor: pointer;
          &.profile {
            margin-bottom: 20px;
            font-size: 20px;
            & > span {
              display: block;
              float: left;
              &:first-of-type {
                width: 50px;
                height: 50px;
                background: @theme-secondary;
                border-radius: 50%;
                color: white;
                span {
                  display: block;
                  padding: 11px;
                  text-align: center;
                }
              }
              &:last-of-type {
                width: calc(~"100% - 50px");
                padding-left: 10px;
                span {
                  display: block;
                  &:first-child {
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                  }
                  &:last-child {
                    font-size: 14px;
                  }
                }
              }
            }
            i {
              position: absolute;
              top: calc(~"50% - 12px");
              right: 0;
            }
          }
          &.active {
            border-bottom-color: @theme-primary;
            color: @theme-primary;
          }
          &:hover {
            opacity: .6;
          }
        }
        &:after {
          position: absolute;
          content: '';
          background: @gray-lighter;
          top: 0;
          left: 100%;
          width: 1px;
          height: calc(~"100% + 50px");
        }
      }
      &:last-child {
        overflow: hidden;
        & > div {
          position: relative;
          width: 400%;
          float: left;
          .transition-all;
          &[data-active="1"] {
            transform: translate3d(-25%,0,0);
          }
          &[data-active="2"] {
            transform: translate3d(-50%,0,0);
          }
          &[data-active="3"] {
            transform: translate3d(-75%,0,0);
          }
          h2 {
            margin-top: 10px;
            margin-bottom: 20px;
            &:nth-of-type(2) {
              margin-top: 40px;
            }
          }
          form {
            button {
              margin-bottom: 20px;
              padding: 4px 15px;
            }
            .form-group {
              font-size: 0;
              & > * {
                display: inline-block;
                vertical-align: middle;
                font-size: 14px;
                &:first-child {
                  width: 33.33%;
                  margin: 0;
                }
                &:last-child {
                  width: 66.66%;
                  &.account > * {
                    border: 1px solid @gray-light;
                    border-radius: 3px;
                    padding: 5px 10px;
                  }
                }
                .reveal {
                  cursor: pointer;
                  .material-icons {
                    line-height: 1.4;
                    font-size: 15px;
                  }
                }
              }
              select {
                cursor: pointer;
              }
            }
          }
          table {
            th, td {
              vertical-align: middle;
              overflow: hidden;
              word-wrap: break-word;
              &:first-child {
                max-width: 260px;
              }
            }
          }
        }
        &:before {
          position: absolute;
          content: '';
          background: white;
          top: 0;
          left: 0;
          width: 17px;
          border-left: 1px solid @gray-lighter;
          height: calc(~"100% + 50px");
        }
        &:after {
          position: absolute;
          content: '';
          background: white;
          top: 0;
          right: 0;
          width: 18px;
          height: calc(~"100% + 50px");
        }
      }
    }
  }
  &.bg-secondary {
    color: white;
  }
}

aside {
  padding: 50px 0;
}

.no-padding {
  padding: 0;
}

// Navigation
.navbar-default {
  background-color: white;
  border-color: fade(@gray-darker, 5%);
  .sans-serif-font;
  .transition-all;
  .navbar-header .navbar-brand {
    position: relative;
    width: 105px;
    height: 40px;
    padding-top: 5px;
    padding-bottom: 5px;
    color: @theme-primary;
    .sans-serif-font;
    font-weight: 700;
    text-transform: uppercase;
    &:hover,
    &:focus {
      color: darken(@theme-primary, 10%);
    }
    img {
      position: absolute;
      left: auto;
      .transition-all;
      &:last-child {
        visibility: hidden;
        opacity: 0;
      }
    }
    box-sizing: content-box;
  }
  &.affix,
  &.navbar-bg {
    .navbar-header .navbar-brand img {
      &:first-child {
        visibility: hidden;
        opacity: 0;
      }
      &:last-child {
        visibility: visible;
        opacity: 1;
      }
    }
  }
  .navbar-header .navbar-toggle {
    font-weight: 700;
    font-size: 12px;
    color: @gray-darker;
    text-transform: uppercase;
  }
  .nav {
    > li {
      > a,
      > a:focus {
        text-transform: uppercase;
        font-weight: 700;
        font-size: 13px;
        color: @gray-darker;
        &:hover {
          color: @theme-primary;
        }
        .material-icons {
          margin-right: 5px;
          font-size: 20px;
          vertical-align: bottom;
        }
      }
      &.active {
        > a,
        > a:focus {
          color: @theme-primary !important;
          background-color: transparent;
          &:hover {
            background-color: transparent;
          }
        }
      }
      &:hover .dropdown-menu {
        visibility: visible;
        opacity: 1;
      }
      & .dropdown-menu {
        display: block;
        visibility: hidden;
        opacity: 0;
        .transition-all;
        right: auto;
        left: 0;
      }
    }
  }
  .login {
    cursor: pointer;
  }
  @media (min-width: 768px) {
    background-color: transparent;
    border-color: fade(white, 30%);
    .navbar-header .navbar-brand {
      color: fade(white, 70%);
      &:hover,
      &:focus {
        color: white;
      }
    }
    .nav > li > a,
    .nav > li > a:focus {
      color: fade(white, 70%);
      &:hover {
        color: white;
      }
    }
    &.navbar-bg,
    &.affix {
      background-color: white;
      border-color: fade(@gray-darker, 5%);
      .navbar-header .navbar-brand {
        color: @theme-primary;
        font-size: 14px;
        &:hover,
        &:focus {
          color: darken(@theme-primary, 10%);
        }
      }
      .nav > li > a,
      .nav > li > a:focus {
        color: @gray-darker;
        &:hover {
          color: @theme-primary;
        }
      }
      .dropdown.open > a:focus {
        background: rgba(166, 166, 166, 0.28);
      }
    }
    .dropdown.open > a:focus {
      background: rgba(51, 51, 51, 0.48);
      color: #ffffff;
    }
  }
  @media (max-width: 768px) {
    .navbar-header .navbar-brand img,
    &.affix .navbar-header .navbar-brand img {
      &:first-child {
        display: none;
      }
      &:last-child {
        visibility: visible;
        opacity: 1;
      }
    }
  }
}

// Homepage Header
header {
  position: relative;
  .information {
    position: absolute;
    width: 1170px;
    top: 25%;
    left: calc(~"50% - 585px");
    pointer-events: none;
    div {
      position: relative;
      background: @theme-secondary;
      box-shadow: 0 8px 6px -6px black;
      width: 300px;
      height: 400px;
      border-top: 20px solid @theme-secondary;
      border-bottom: 20px solid @theme-secondary;
      color: white;
      overflow: hidden;
      pointer-events: auto;
      h3 {
        margin: 0;
        margin-bottom: 20px;
        text-align: center;
      }
      ul {
        padding: 0 20px;
        list-style: none;
        height: 314px;
        overflow: auto;
        a {
          display: block;
          .transition-all;
          padding-bottom: 15px;
          text-decoration: none;
          &:hover {
            opacity: .6;
          }
          b {
            color: @orange-middle;
          }
          img {
            width: 100%;
            margin: 5px 0;
          }
          span {
            display: block;
            font-size: 12px;
            color: white;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
        }
      }
    }
  }
}

// Homepage Carousel
.carousel {
  .item {
    background-color: #777;
  }
}

.carousel-caption {
  top: 40%;
  left: 20%;
  right: 20%;
  bottom: auto;
}

.carousel-inner > .item > img {
  width: 100%;
}

// Sections
.section-heading {
  margin-top: 0;
}

.service-box {
  max-width: 400px;
  margin: 50px auto 0;
  @media (min-width: 992px) {
    margin: 20px auto 0;
  }
  p {
    margin-bottom: 0;
  }
}

.portfolio-box {
  position: relative;
  display: block;
  max-width: 650px;
  margin: 0 auto;
  .portfolio-box-caption {
    color: white;
    opacity: 0;
    display: block;
    background: fade(@theme-primary, 90%);
    position: absolute;
    bottom: 0;
    text-align: center;
    width: 100%;
    height: 100%;
    .transition-all;
    .portfolio-box-caption-content {
      width: 100%;
      text-align: center;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      .project-category,
      .project-name {
        .sans-serif-font;
        padding: 0 15px;
      }
      .project-category {
        text-transform: uppercase;
        font-weight: 600;
        font-size: 14px;
      }
      .project-name {
        font-size: 18px;
      }
    }
  }
  &:hover {
    .portfolio-box-caption {
      opacity: 1;
    }
  }
  &:focus {
    outline: none;
  }
  @media (min-width: 768px) {
    .portfolio-box-caption {
      .portfolio-box-caption-content {
        .project-category {
          font-size: 16px;
        }
        .project-name {
          font-size: 22px;
        }
      }
    }
  }
}

.call-to-action {
  h2 {
    margin: 0 auto 20px;
  }
}

#contact {
  i.fa {
    color: @brand-primary;
  }
}

//Footer
footer {
  padding: 10px 0;
  .container {
    padding-top: 10px;
    border-top: 1px solid @gray-lighter;
    img {
      margin-left: 10px;
      float: right;
    }
  }
}
//End of Footer

//Login Form
.loginform {
  position: fixed;
  display: none;
  table-layout: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0,0,0,.6);
  z-index: 2000;
  .fa-close {
    position: absolute;
    top: 50px;
    right: 75px;
    color: @gray-lighter;
    &:hover {
      cursor: pointer;
      text-decoration: none;
    }
  }
  form {
    background: @gray;
    width: 480px;
    border-radius: 0 0 3px 3px;
    margin: auto;
    padding: 20px 30px 30px;
    color: white;
    & > * {
      display: block;
      width: 100%;
    }
    img {
      width: auto;
      margin: auto;
      margin-bottom: 5px;
    }
    input {
      background: none;
      border: none;
      border-bottom: 1px solid @gray-light;
      margin-bottom: 10px;
      outline: none;
      &:focus {
        border-bottom-color: @orange-middle;
      }
    }
    .forgotpassword {
      margin-bottom: 30px;
      a {
        color: white;
      }
    }
  }
  b {
    display: block;
    background: white;
    width: 480px;
    border-radius: 3px 3px 0 0;
    margin: auto;
    padding: 10px;
    text-align: center;
  }
  &.active {
    display: table;
  }
  &.fade {
    .transition-all;
    opacity: 0;
  }
  &.fade.in {
    opacity: 1;
  }
  & > div {
    display: table-cell;
    width: 100%;
    vertical-align: middle;
  }
}
//End of Login Form

.no-gutter > [class*='col-'] {
  padding-right:0;
  padding-left:0;
}